/*Navigointivalikon tyylit*/
.navbar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    padding: 1rem 7%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: var(--box-shadow);
    z-index: 10000;
    flex-wrap: wrap;
}

.navbar a {
    font-size: 1.4rem;
    padding: 1rem;
    color: var(--light-color);
    border: .1rem solid transparent;
    font-weight: 200;
    text-decoration: none;
    color: #fff;
}

.navbar a:hover {
    color: #fff;
    font-size: 150%;
    border-radius: 2px;
}

.textContainer {
    align-items: center;
    justify-content: center;
    padding-top: 300px;

}

.allArticles {
    display: flex;
    margin: 0px;
    padding: 5px;
    background-color: white;
    align-items: center;
}

.overLay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(7, 0, 42, 0.2);
}

